<template>
  <div class="org_main">
  <el-row :gutter="24" type="flex" class="row-bg" justify="right">
    <el-col :xs="18" :sm="18" :md="18" :lg="12" :xl="12" :offset="5">
	<div style="margin-top: 15px;">
  <el-input placeholder="Search Events" v-model="search" class="input-with-select">
    <el-button slot="append" icon="el-icon-search" @click="sendSearch(search)" ></el-button>
  </el-input>
	<p><i>Search organization names.</i></p>
	</div>
    </el-col>
  </el-row>

    <el-row v-for="o in orgs" :gutter="24" type="flex" class="row-bg" justify="center">
      <el-col :span="18" class="baseBorder">
        <h2><a :href="'organization/' + o.id">{{o.name}}</a></h2> {{o.jurisdiction.name}}
	<el-row :span="18"> 

	  <div v-if="o.memberships.length > 0">
	    <el-collapse @change="handleChange">
	      <el-collapse-item>
		<template slot="title"><h3>Memberships</h3></template>
		<div v-for="m in o.memberships">
		  <a :href="'/person/' + m.person.id">{{m.person.name}}</a>
		</Div>
	      </el-collapse-item>
	    </el-collapse>
	  </div>

	</el-row>
      </el-col> 
    </el-row>

    <el-row :gutter="24" v-if="next !== null" type="flex" class="row-bg" justify="center">
      {{this.orgs.length}}<h1><a v-on:click="getMore(next)"><i class="el-icon-arrow-down"></i></a></h1>{{count}}
    </el-row>


  </div>
</template>


<script>
export default {
  name: 'Organizations',
  data () {
    return {
      msg: 'Organizations',
      next: '',
      count: '',      
      orgs: [],
    }
  },
  methods: {
    handleChange(val) {
        console.log(val);
    },
  getMore: function(next){
      next = next.replace('http', 'https');	  
      this.$http.get(next)
      .then(function(response){
        console.log(response);
	this.orgs = this.orgs.concat(response.data.results);
	this.next = response.data.next
      });
    },
    sendSearch: function(search){
      console.log('clicked', search);
      var nsearch = 'https://api.mnactivist.org/api/searchorgs?q=' + search;
      this.$http.get(nsearch)
      .then(function(response){
        console.log(response);
	this.orgs = response.data.results;
	this.count = response.data.count;
	this.next = response.data.next	
      });
    }
},
  created: function(){
      console.log('created ran');
      this.$http.get('https://api.mnactivist.org/api/organizations')
//      this.$http.get('http://localhost:8000/api/organizations')
      .then(function(response){
	this.orgs = response.data.results;
	this.next = response.data.next
	this.count = response.data.count;	
      });
  }
}
</script>

<style scoped>
.baseBorder {
    border: 0px #000 solid;
    border-radius: 25px;
    padding: 0px 5px 25px 5px;
    margin: 15px;	
}
h1, h2 {
  font-weight: normal;
}
a {
  color: #42b983;
}
</style>



