<template>
  <div class="bills-div">
  <el-row :gutter="24" type="flex" class="row-bg" justify="right">
    <el-col :xs="18" :sm="18" :md="18" :lg="12" :xl="12" :offset="5">
	<div style="margin-top: 15px;">
  <el-input placeholder="Search Events" v-model="search" class="input-with-select">
    <el-button slot="append" icon="el-icon-search" @click="sendSearch(search)" ></el-button>
  </el-input>
	<p><i>Search bill identifiers and titles</i></p>
	</div>
    </el-col>
  </el-row>

  <el-row v-for="o in bills" :gutter="24" type="flex" class="row-bg" justify="center">
      <el-col :span="18" class="bills">
          <h2><a :href="'policy/' + o.id">{{o.identifier}}</a></h2>
	  {{o.from_organization.name}} 
	  <p>{{o.title}}</p>
	  <p><a :href="o.sources[0].url">Link</a></p>


<el-collapse @change="handleChange">
  <el-collapse-item>
  	<template slot="title"><h3>Actions</h3></template>
	  <div v-for="a in o.actions" v-if="a.description !== 'passed_lower' && a.description !== 'passed_upper'">
	     {{a.description}}
	     {{a.date | moment("MM/D/YYYY")}}	     
	  </div>
  </el-collapse-item>
</el-collapse>
</el-col>    
</el-row>    

  <el-row :gutter="24" v-if="next !== null" type="flex" class="row-bg" justify="center">
  {{this.bills.length}}<h1><a v-on:click="getMore(next)"><i class="el-icon-arrow-down"></i></a></h1>{{count}}
  </el-row>


</div>
</template>

<script>
export default {
  name: 'Bills',
  data () {
    return {
      msg: 'Bills',
      post_label: 'District',
      next: '',
      count: '',      
      bills: [],
    }
  },
  methods: {
    handleChange(val) {
        console.log(val);
    },
  getMore: function(next){
      next = next.replace('http', 'https');	
      this.$http.get(next)
      .then(function(response){
        console.log(response);
	this.bills = this.bills.concat(response.data.results);
	this.next = response.data.next
      });
    },
    sendSearch: function(search){
      console.log('clicked', search);
      var nsearch = 'https://api.mnactivist.org/api/searchpolicy?q=' + search;
      this.$http.get(nsearch)
      .then(function(response){
        console.log(response);
	this.bills = response.data.results;
	this.count = response.data.count;
	this.next = response.data.next	
      });
    }
  },
  created: function(){
      console.log('created ran');
      this.$http.get('https://api.mnactivist.org/api/bills')
//      this.$http.get('http://localhost:8000/api/bills')
      .then(function(response){
        this.bills = response.data.results;
	this.next = response.data.next;
	this.count = response.data.count;	
      });
  },
}
</script>


<style scoped>
h1, h2 {
  font-weight: normal;
  color: #42b983;	
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.bills {
  border: 0px #000 solid;
  border-radius: 25px;
  padding: 0px 5px 25px 5px;
  margin: 15px;
}
</style>



