<template>
  <div class="People">
  <el-row :gutter="24" type="flex" class="row-bg" justify="right">
    <el-col :xs="18" :sm="18" :md="18" :lg="12" :xl="12" :offset="5">
	<div style="margin-top: 15px;">
  <el-input placeholder="Search Events" v-model="search" class="input-with-select">
    <el-button slot="append" icon="el-icon-search" @click="sendSearch(search)" ></el-button>
  </el-input>
	<p><i>Search names.</i></p>
	</div>
    </el-col>
  </el-row>
  
    <el-row v-for="p in people" :gutter="24" type="flex" class="row-bg" justify="center">

      <el-col :span="18" class="cpeople">
	<h2><a :href="'people/' + p.id">{{p.name}}</a></h2>
	<el-row :span="18"> 
	  <div v-for="m in p.memberships" v-if="m.post !== null">
	    {{m.post.role}} from District {{m.post.label}}
	  </div>
	</el-row>
	<div v-if="p.memberships.length > 1">	  
	  <el-collapse @change="handleChange">
	    <el-collapse-item>			  
	      <template slot="title"><h3>Memberships</h3></template>
	      <div v-for="m in p.memberships" v-if="m.post == null">
		<a :href="'organization/' + m.organization.id">{{m.organization.name}}</a>
	      </div>
	    </el-collapse-item>
	  </el-collapse>
	</div>
      </el-col> 

    </el-row>
    
  <el-row :gutter="24" v-if="next !== null" type="flex" class="row-bg" justify="center">
  {{this.people.length}}<h1><a v-on:click="getMore(next)"><i class="el-icon-arrow-down"></i></a></h1>{{count}}
  </el-row>


  </div>
</template>

<script>
export default {
  name: 'People',
  data () {
    return {
      msg: 'People',
      next: '',
      count: '',      
      people: [],
    }
  },
  methods: {
    handleChange(val) {
        console.log(val);
    },
  getMore: function(next){
      this.$http.get(next)
      .then(function(response){
        console.log(response);
	this.people = this.people.concat(response.data.results);
	this.next = response.data.next.replace('http', 'https');		
      });
    },
    sendSearch: function(search){
      console.log('clicked', search);
      var nsearch = 'https://api.mnactivist.org/api/searchpeople?q=' + search;
      this.$http.get(nsearch)
      .then(function(response){
        console.log(response);
	this.people = response.data.results;
	this.count = response.data.count;
	this.next = response.data.next	
      });
    }
  },  
  created: function(){
      console.log('created ran');
      this.$http.get('https://api.mnactivist.org/api/people')
//      this.$http.get('http://localhost:8000/api/people')
      .then(function(response){
        this.people = response.data.results;
	this.next = response.data.next.replace('http', 'https');		
	this.count = response.data.count;
      });
  },

}
</script>


<style scoped>
h1, h2 {
  font-weight: normal;
  color: #42b983;	
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.cpeople {
  border: 0px #000 solid;
  border-radius: 25px;
  padding: 0px 5px 25px 5px;
  margin: 15px;
}
</style>



